// COLORS
$white: #fff;
$black: #000;
$grey: #777;
$dirty-white: #f4f4f4;
$light-grey: #F9F8F8;
$light-grey-1: #D5D5D5;
$dark-grey: #29323c;
$grey-text: #646464;
$text: #4e4a67;

// PALLETTE

$color-orange-1: #fc6076;
$color-orange-2: #ff9a44;

$color-blue-1: #0acffe;
$color-blue-2: #495aff;

$color-purple-1: #667eea;
$color-purple-2: #764ba2;

$color-1: #4FDF93;
$color-2: #08FCF4;
$color-3: #26B1E4;
$color-4: #205072;
$color-5: #201c2d;
$color-6: #29323c;
// SHADOWS
$shadow-1: rgba(0, 0, 0, 0.1);
$shadow-2: rgba(0, 0, 0, 0.2);
$shadow-4: rgba(0, 0, 0, 0.4);
$shadow-6: rgba(0, 0, 0, 0.6);
$shadow-8: rgba(0, 0, 0, 0.8);

// FONTS
$default-text-size: 1.8rem;

// GRADIENTS
$green-gradient: linear-gradient(147deg, $color-2, $color-3);
$orange-gradient: linear-gradient(147deg, $color-orange-1, $color-orange-2);
$blue-gradient: linear-gradient(147deg, $color-blue-1, $color-blue-2);
$purple-gradient: linear-gradient(147deg, $color-purple-1, $color-purple-2);
$light-gradient: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
$silver-gradient: linear-gradient(147deg, #e6e9f0 0%, #fff 100%);
$lg-gradient: linear-gradient(147deg, #f2f2fd 0%, #fdfbfd 100%);
$green-gradient-2: linear-gradient(to right, #43e97b 0%, #38f9d7 100%);

$color-primary-1: #11282f;
$color-primary-2: #2e6170;

$color-secondary-1: #1bbb4e;
$color-secondary-2: #65f09e;

$color-tertiary-1: #189261;
$color-tertiary-2: #03ef8e;