// BOOTSTRAP BREAKPOINTS

@mixin respond($breakpoint) {
    @if $breakpoint==media-sm {
        @media (max-width: 576px) {
            @content;
        }
    }

    @if $breakpoint==media-md {
        @media (max-width: 768px) {
            @content;
        }
    }

    @if $breakpoint==media-lg {
        @media (max-width: 992px) {
            @content;
        }
    }

    @if $breakpoint==media-xl {
        @media (max-width: 1200px) {
            @content;
        }
    }
}

// CENTERING
@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin absCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


// FONT SIZE MIXIN
$display-wide: 1920;
$display-small: 375;

@mixin fluid($property, $minValue, $maxValue) {
    $x: ($maxValue - $minValue)/($display-wide - $display-small);
    $y: $maxValue - $display-wide * $x;
    #{$property}: calc(#{100*$x}vw + #{$y}px);
}

// BREAKPOINTS
$breakpoints: ("phone": 400px,
    "phone-wide": 480px,
    "phablet": 560px,
    "tablet-small": 640px,
    "tablet": 768px,
    "tablet-wide": 1024px,
    "desktop": 1248px,
    "desktop-wide": 1440px);

@mixin mq($width, $type: min) {
    @if map_has_key($breakpoints, $width) {
        $width: map_get($breakpoints, $width);

        @if $type==max {
            $width: $width - 1px;
        }

        @media only screen and (#{$type}-width: $width) {
            @content;
        }
    }
}

// PSEUDO

@mixin pseudo($display: block, $pos: absolute, $content: '') {
    content: $content;
    display: $display;
    position: $pos;
}