.heading-box {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

    // border: 1px dashed red;

    &--center {
        justify-content: flex-end;
        height: 100%;
    }

    &--left {
        align-items: flex-start;
    }

    &--right {
        align-items: flex-end;
    }
}

.primary-heading {
    line-height: 1;
    display: inline-block;

    &--top {
        display: block;
        text-shadow: .3rem .3rem .3rem $shadow-2;
        @include fluid('font-size', 34, 75);
        @include fluid('margin-bottom', 5, 15);
        font-family: 'Dosis', sans-serif;

        span {
            color: $color-blue-2;
        }
    }

    &--bottom {
        display: block;
        @include fluid('font-size', 18, 28);
        font-weight: 400;
    }

    &__border {
        display: block;
        margin: 1.5rem auto;
        width: 80%;
        height: 3px;
        border-radius: 100rem;
        background-color: $color-1;
        background-image: $blue-gradient;
        background-image: $green-gradient-2;
        box-shadow: 0 1px .3rem rgba(#666, .3);
    }

    &__img {
        height: 5rem;
        fill: $black;
    }

}