.section-about {
    border: 2px dashed $light-grey-1;
    padding: 20rem 0 18rem 0;
}

.section-showcase {
    height: 100vh;
    font-size: 6rem;
    color: $text;
    text-shadow: 0 3px 3px $light-grey-1;
    @include flexCenter;
    border-bottom: 1px dashed $light-grey-1;
}

.section-contact {
    position: relative;
    padding: 20rem 0 15rem 0;

    @include respond(media-sm) {
        padding: 10rem 0 10rem 0;
    }
}



.copyright {
    text-align: center;
    position: absolute;
    bottom: 0;
    display: block;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;

    &__text {
        padding: 2rem;
        @include fluid('font-size', 12, 18);
    }
}