.menu {
    position: fixed;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    z-index: 15;
    @include fluid('padding', 10, 20);

    @include respond(media-sm) {
        justify-content: center;
    }


    &__nav {
        background-image: $lg-gradient;
        box-shadow: 0 1rem 4rem $shadow-2;
        border-radius: 1rem;
        padding: 2rem;
        transition: all .3s;

        display: inline-block;

    }

    &__list {
        list-style: none;
        display: flex;
    }

    &__item {
        margin: 0 2rem;

        @include respond(media-sm) {
            margin: 0 1.5rem;
        }
    }

    &__link {

        &:link,
        &:visited {
            text-decoration: none;
            color: $text;
            transition: all .2s;
        }

        &:hover,
        &:active {
            cursor: pointer;
            color: $color-orange-2;
        }
    }


    .active {
        border-bottom: 3px solid $color-orange-1;
    }

}

// DOWN ARROW

.chevron-wrapper {
    height: 100%;
    @include flexCenter;
}

.chevron-down {
    height: 5rem;
    fill: $light-grey-1;
    animation: mover 2s infinite;

    &--about {
        height: 15rem;
    }
}


// HIDE MENU
.scrollUp {
    transform: translateY(-12rem);
}