* {
    margin: 0;
    padding: 0;
    box-sizing: inherit;


    // ? testing
    // border: 1px solid red;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;

    @include respond(media-sm) {
        font-size: 50%;
    }
}

body {
    box-sizing: border-box;
    font-weight: 400;
    font-size: $default-text-size;
    font-family: 'Brandon Grotesque', sans-serif;
    color: $text;
    @include fluid('font-size', 18, 22);
    line-height: 1.5;
    position: relative;
    background-image: $silver-gradient;
    // background-image: $lg-gradient;

}

::-webkit-scrollbar {
    display: none;
}