.color-panel {
    @include fluid('height', 90, 150);
    @include fluid('width', 90, 150);
    @include fluid('top', -30, -50);
    @include fluid('right', 30, 40);
    position: absolute;

    &--1,
    &--2,
    &--3 {
        height: 100%;
        width: 100%;
        position: absolute;
        box-shadow: 0 1rem 2rem $shadow-2;
        border-radius: 5px;
    }

    &--1 {
        background-image: $lg-gradient;
        transform: rotate(5deg);

    }

    &--2 {
        background-image: $green-gradient-2;
        transform: rotate(15deg);
    }

    &--3 {
        background-image: $blue-gradient;
        transform: rotate(-5deg);
    }

    &__icon {
        @include absCenter;
        @include fluid('height', 40, 80);
        @include fluid('width', 40, 80);
        fill: $dirty-white;
    }
}