.contact {
    box-shadow: 0 1rem 4rem $shadow-2;
    border-radius: 1rem;
    position: relative;
    padding: 10rem 6rem 4rem 6rem;

    @include respond(media-sm) {
        padding: 8rem 6rem 4rem 6rem;
    }

    &__details {
        text-align: center;

        @include respond(media-sm) {
            margin-top: 3rem;
        }
    }

    &__name {
        font-family: 'Dosis', sans-serif;
    }

    &__mail {

        &:link,
        &:visited {
            text-decoration: none;
            color: $color-purple-1;
            transition: color .2s;
        }

        &:hover {
            color: $color-1;

        }
    }

    &__text {
        margin-top: 2rem;
    }

    &__social {}

    &__link {

        &:link,
        &:visited {
            text-decoration: none;
            margin: 0 2rem;
        }
    }

    &__icon {
        height: 3.5rem;
        width: 3.5rem;
        fill: $blue-gradient;
        fill: $light-grey-1;
        transition: fill .3s;

        &:hover {
            fill: #4269ff;
        }
    }
}