.icon-box {
    position: absolute;
    @include fluid('top', -50, -80);
    @include fluid('right', 30, 50);

    &__shape {
        background-image: $blue-gradient;
        border-radius: 1rem;
        @include fluid('height', 90, 150);
        @include fluid('width', 90, 150);
        transform: skewX(-10deg);
        box-shadow: 0 1rem 3rem $shadow-2;
        position: relative;
        @include flexCenter;
        z-index: 3;

        &--1,
        &--2 {
            position: absolute;
        }

        &--1 {
            background-image: $green-gradient-2;
            transform: skewX(-10deg) rotate(15deg);
            top: 0;
            z-index: 2;
        }

        &--2 {
            background-image: $lg-gradient;
            transform: skewX(-10deg) rotate(-15deg);
            z-index: 1;
            top: 0;
        }
    }

    &__icon {
        fill: $dirty-white;
        transform: skewX(10deg);
        @include fluid('height', 40, 80);
        @include fluid('width', 40, 80);
    }
}