.header {
    height: 100vh;
    position: relative;
    @include flexCenter;

    @include respond(media-sm) {
        padding: 15rem 9rem 0 9rem;
    }
}


// Hero Image Composition

.hero {
    position: relative;
    @include fluid('height', 180, 400);
    @include fluid('width', 180, 400);
    margin-bottom: 6rem;
    z-index: 10;


    &__element {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 5px;
        box-shadow: 0 1rem 4rem $shadow-2;

        &--1 {
            background-image: $green-gradient-2;
            transform: rotate(5deg);
            z-index: -2;
        }

        &--2 {
            background-image: $blue-gradient;
            transform: rotate(15deg);
            z-index: -1;
        }

        &--3 {
            background-image: url('/assets/img/ninoslav-stankovic-profile.svg');
            background-color: $dirty-white;
            background-size: 90%;
            background-repeat: no-repeat;
            background-position: center;
            transform: rotate(-5deg);
        }
    }
}